.arena-page {
    display: flex;
    justify-content: center;
    align-items: center;
}

.containers-holder {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 30px;
    padding: 50px 0;
    width: 70%;
    background-color: #2c3038;
    box-shadow: 0 2px 10px #00000085;
}

.arena-screen {
    width: 100%;
}

.arena-screen img {
    height: auto;
    width: 100%;
    border-radius: 3px;
}

.description {
    text-align: center;
    margin: 20px;
    background-color: #fff;
    border-radius: 15px;
    width: 80%;
    padding: 5px 25px 20px;
}

.cards-holder {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    grid-template-areas:
        "bigscreen equipments"
        "seats seats";
    padding: 20px;
    grid-gap: 100px;
    margin: 0 50px;
    text-align: center;
}

.bigscreen {
    grid-area: bigscreen;
}

.equipments {
    grid-area: equipments;
}

.seats {
    grid-area: seats;
}

.bigscreen img,
.equipments img,
.seats img {
    height: 60%;
    width: 90%;
    box-shadow: 0 2px 10px #00000085;
    margin-bottom: 20px;
}

.bigscreen,
.equipments {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 20px 15px;
    height: auto;
    width: 100%;
    border-radius: 20px;
    box-shadow: 0 2px 10px #00000085;
    background-color: #fff;
}

.seats {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: auto;
    padding: 20px 15px;
    border-radius: 20px;
    box-shadow: 0 2px 10px #00000085;
    background-color: #fff;
}

.short-text {
    background-color: #fff;
    box-shadow: 0 2px 10px #00000085;
    border-radius: 10px;
    padding: 5px 10px;
    margin: 20px;
}

.booking-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    background-color: #fff;
    margin-top: 30px;
    padding: 10px 35px 20px;
    border-radius: 20px;
    box-shadow: 0 2px 10px #00000085;
    width: 86%;
}

.booking-container label {
    display: block;
    color: #262729;
    font-weight: bold;
    margin: 10px 0 0;
}

.booking-container input {
    height: 28px;
    border: none;
    box-shadow: 0px 5px 10px #00000085;
}

.booking-container textarea {
    border: none;
    box-shadow: 0px 5px 10px #00000085;
    resize: none;
}

.arena-name,
.arena-email {
    width: 200px;
}

.arena-phone {
    width: 135px;
    padding: 1px 4px;
}

.arena-submit {
    background-color: #4285f4;
    color: #fff;
    border: none;
    padding: 8px 18px;
    border-radius: 5px;
    cursor: pointer;
    margin: 20px 0 0;
    position: relative;
}

.arena-submit:hover {
    background-color: #fff;
    color: #4285f4;
    border: 1px solid #4285f4;
    font-weight: bold;
    transition: 0.7s;
  }


@media (max-width: 1250px) {
    .cards-holder {
        padding: 10px;
        grid-gap: 50px;
        margin: 0 25px;
    }
}

@media (max-width: 1100px) {
    .cards-holder {
        padding: 5px;
        grid-gap: 25px;
        margin: 0 10px;
    }
}

@media (max-width: 950px) {
    .cards-holder {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .bigscreen,
    .equipments,
    .seats {
        width: 80%;
        margin: 20px 50px;
    }

    @media (max-width: 950px) {

        .containers-holder {
            border-radius: 25px;
            width: 80%;
        }

        .event-description,
        .event-additional {
            width: 100%;
            max-width: 100%;
        }

        .arena-name,
        .arena-email {
            width: 100%;
            max-width: 100%;
        }

    }

    @media (max-width: 750px) {
        .booking-container {
            width: 85%;
            padding: 10px 20px 20px;
        }
    }

    @media (max-width: 550px) {
        .booking-container {
            width: 90%;
        }

        .containers-holder {
            border-radius: 20px;
            width: 90%;
        }

        .bigscreen,
        .equipments,
        .seats {
            width: 95%;
            margin: 20px 10px;
        }

        .booking-container label {
            font-size: 13px;
        }
    }

    @media (max-width: 400px) {
        .cards-holder {
            margin: 0;
        }

        .containers-holder {
            width: 95%;
        }
    }




}